
import React from 'react';
import logo from './logo.svg';
import './App.css';
import ListItems from './listItem.js';


class App extends React.Component{
  constructor(props){
    super(props);
    this.state={
      items:[],
      currentItem:{
        text:'',
        key:''
      }
    }
    this.handleInput=this.handleInput.bind(this);
    this.addItem=this.addItem.bind(this);
    this.deleteItem=this.deleteItem.bind(this);
  }

  handleInput(e){
    e.preventDefault();
   this.setState({
     currentItem:{
       text:e.target.value,
       key:Date.now()
     }
   })
  }
addItem(e){
 e.preventDefault();
 const newItem=this.state.currentItem;

 if(newItem.text!== ""){
   const newitems=[...this.state.items,newItem];
   this.setState({
     items:newitems,
     currentItem:{
       text:'',
       key:''
     }
   })
 }
}
deleteItem(key){
  const filteredItems=this.state.items.filter(itm=>
    itm.key !==key);
    this.setState({
      items:filteredItems
  })
}
  render(){
    return (
      <div className="App" onSubmit={this.addItem}>
      <header>
         <form id="todoForm">
             <input type="text" placeholder="enter text" value={this.state.currentItem.text} onChange={this.handleInput}/>
             <button type="submit">Add</button>

         </form>
      </header>
      <ListItems items={this.state.items}
      deleteItem={this.deleteItem}>
      </ListItems>

       </div>
    )
  }
}

export default App;
